<template>
	<view>
		<view v-if="list.length" class="w-100 flex justify-center mt-2 align-center flex-column">
			<block v-for="(item,index) in list" :key="index">
				<view class="border p-3 mb-2 rounded position-relative" :class="current == index ? 'active' : 'bg-white'" style="width: 96%;"
				 @click="handleTapItem(index)">
					<view class="flex flex-column">
						<text>设备名称: {{item.e_name}}</text>
						<text>设备类型: {{item.e_type}}</text>
						<text>到期时间: {{item.return_time.substr(0,10)}}</text>
					</view>
				</view>
			</block>
		</view>
		<view v-else>
			<u-empty text="消息列表为空" mode="message" icon-size="80" margin-top="120"></u-empty>
		</view>
		<u-tabbar :list="tabBer" :mid-button="$store.state.midBtn" :active-color="activeColor" :inactive-color="inactiveColor"></u-tabbar>
	</view>
</template>

<script>
	import tool from '../../../utils/util.js';
	export default {
		data() {
			return {
				list: [],
				current: -1,
				inactiveColor: '#000',
				activeColor: '#f07373'
			}
		},
		onShow() {
			this.handleDeviceInformation();
			this.$store.commit('tabBerList')
		},
		computed:{
			tabBer() {
				return this.$store.state.tabBarlist
			}
		},
		methods: {
			handleDeviceInformation(y_id) {
				this.$u
					.post('/GetAllEquipmentsByPid', {
						pregnant_id: y_id
					})
					.then(res => {
						let _list = res.data;
						uni.setStorage({
							key: 'equipment_ids',
							data: _list
						})
						let time = tool.getFtSystemTime().substr(0, 10).replace(/-/g, '/');
						uni.getStorage({
							key: 'equipment_ids',
							success: res => {
								let arr = res.data,
									_arr = [];
								for (let item of arr) {
									let str = item.return_time.substr(0, 10);
									let date = new Date(str);
									date = new Date(date.setDate(date.getDate() + 1));
									str = date.getFullYear() + "/" + ((date.getMonth() + 1) > 9 ? date.getMonth() + 1 : "0" + (date.getMonth() +
										1)) + "/" + (date.getDate() > 9 ? date.getDate() : "0" + date.getDate());
					
									if ((new Date(str) - new Date(time)) / (24 * 3600 * 1000) + 1 <= 3 && item.status == '已领用') {
										_arr.push(item)
										this.list = _arr;
									}else{
										this.list = []
									}
								}
							}
						})
					});
			},
			handleTapItem(index) {
				this.current = index;
			}
		}
	}
</script>

<style scoped>
	.active {
		background-color: #f8f9fa;
	}
</style>
